import React from 'react';
import './Button.component.scss';

export default function Button({ type, text, status, bgColor, color, width }) {
  return (
    <button
      className="form-send-button"
      type={type}
      style={{ backgroundColor: bgColor, color: color, width: width }}
    >
      <p className="btn">{text ? text : status}</p>
    </button>
  );
}
