import React from 'react';
import './Input.component.scss';

export default function Input({
  label,
  name,
  type,
  value,
  placeholder,
  onChange,
  error,
}) {
  return (
    <div className="input">
      <label>
        <p className="caption">{label}</p>
      </label>
      <input
        type={type}
        placeholder={placeholder}
        onChange={onChange}
        value={value}
        name={name}
      />
      <p className="small error">{error}</p>
    </div>
  );
}
