import React, { useState, useEffect, useContext, useRef } from 'react';
import axios from 'axios';
import './Form.component.scss';
import variables from '../../styles/_variables.module.scss';
import Sectionh2 from '../SectionH2/Sectionh2.component.jsx';
import Input from '../Input/Input.component.jsx';
import Textarea from '../Textarea/Textarea.component.jsx';
import Button from '../Button/Button.component.jsx';
import ReCAPTCHA from 'react-google-recaptcha';
import { AppContext } from '../../AppContext';

export default function Form() {
  const initialValues = { name: '', email: '', message: '' };
  const [formValues, setFormValues] = useState(initialValues);
  const [formErrors, setFormErrors] = useState({});
  const [isSubmit, setIsSubmit] = useState(false);
  const [isValid, setIsValid] = useState(false);
  const [status, setStatus] = useState('SEND INQUIRY');
  const { themeColorObj } = useContext(AppContext);
  const themeColor = themeColorObj[0];

  const recaptchaRef = useRef();

  const mailformat =
    /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormValues({ ...formValues, [name]: value });
  };

  const validate = (values) => {
    const errors = {};
    if (!values.name) {
      errors.name = 'Your name is required!';
    }
    if (!values.email) {
      errors.email = 'Your email is required!';
    } else if (!mailformat.test(values.email)) {
      errors.email = 'Your email is not valid!';
    }
    if (!values.message) {
      errors.message = 'A message is required!';
    }
    return errors;
  };

  const resetValues = () => {
    setTimeout(function () {
      setStatus('SEND INQUIRY');
      setIsSubmit(false);
      setIsValid(false);
      setFormValues(initialValues);
    }, 3000);
  };

  const sendForm = async () => {
    try {
      const token = await recaptchaRef.current.executeAsync();
      const { name, email, message } = formValues;
      await axios.post("/contact", {
        name,
        email,
        message,
        token,
      });
    } catch (err) {
        console.error(err)
    }
  }

  const handleSubmit = (event) => {
    event.preventDefault();
    setIsSubmit(true);
    setFormErrors(validate(formValues));
  };

  useEffect(() => {
    if(Object.keys(formErrors).length === 0 && isSubmit) { 
      setIsValid(true);
    } else if (Object.keys(formErrors).length !== 0 && isSubmit){
      setStatus('TRY AGAIN!')
    }
  }, [formErrors, isSubmit])
  

  useEffect(() => {
    if (isValid) {
      setStatus('THANK YOU!');
      sendForm();
      resetValues();
    }
  }, [isValid])
 

  return (
    <form onSubmit={handleSubmit}>
      <Sectionh2 h2={'Send your inquiry'} />
      <Input
        label={'name'}
        type={'text'}
        placeholder={'Your name'}
        onChange={handleChange}
        name={'name'}
        value={formValues.name}
        error={formErrors.name}
      />
      <Input
        label={'email'}
        type={'email'}
        placeholder={'Your email address'}
        onChange={handleChange}
        name={'email'}
        value={formValues.email}
        error={formErrors.email}
      />
      <Textarea
        label={'message'}
        placeholder={'Please type your message'}
        onChange={handleChange}
        name={'message'}
        value={formValues.message}
        error={formErrors.message}
      />
      <div>
        <p className="small">
          This site is protected by reCAPTCHA and the Google
          <a
            href="https://policies.google.com/privacy"
            style={{ color: `${variables.secondaryColorDark}` }}
          >
            {' '}
            Privacy Policy
          </a>{' '}
          and
          <a
            href="https://policies.google.com/terms"
            style={{ color: `${variables.secondaryColorDark}` }}
          >
            {' '}
            Terms of Service
          </a>{' '}
          apply.
        </p>
      </div>
      <Button
        type={'submit'}
        status={status}
        bgColor={variables.secondaryColor}
        colors={variables.primaryColor}
        width={'100%'}
      />
      <ReCAPTCHA
        ref={recaptchaRef}
        sitekey={'6LeMJE4eAAAAAMTccweQVhjvYKhRjfAO9wrVFsQI'}
        theme={themeColor === 'light' ? 'light' : 'dark'}
        size="invisible"
      />
    </form>
  );
}
